import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Avatar,
  Button,
  Tooltip,
  Typography,
  IconButton
} from '@mui/material';
import React from 'react';
import TaskAltTwoToneIcon from '@mui/icons-material/TaskAltRounded';
import SyncTwoToneIcon from '@mui/icons-material/SyncRounded';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CancelTwoToneIcon from '@mui/icons-material/CancelRounded';
import DeleteIcon from '@mui/icons-material/DeleteRounded';
import { ShowUpAnimated } from 'components/animated/showUp';
import { green } from '@mui/material/colors';

const statusTexts = ['سپرده شده', 'درحال انجام', 'انجام شده', 'کنسل شده'];

export function TasksView(props) {
  console.log(props);
  return (
    <div className="row">
      <div className="col-12">
        {!props.tasks.length ? (
          <Typography align="center" variant="h5" className="p-4">
            تسکی وجود ندارد
          </Typography>
        ) : (
          props.tasks.map((item, index) => (
            <Accordion key={`task-${index}-${item._id}`}>
              <AccordionSummary
                expandIcon={
                  <ShowUpAnimated right delay={index / 4}>
                    <ExpandMoreIcon color="secondary" />
                  </ShowUpAnimated>
                }>
                <div className="w-100 ">
                  <div className="row">
                    <div className="col-2 col-sm-1 py-2">
                      <Tooltip title={statusTexts[item.Status]}>
                        <div className={'me-2 status shadow status-' + item.Status}></div>
                      </Tooltip>
                    </div>
                    <div className="col">
                      <div className="row">
                        <div className="col-6 pt-2">
                          <Typography variant="body1">{item.Title}</Typography>
                        </div>
                        <div className="col-6 text-start pt-2">
                          <div className="row">
                            <div className="col">
                              <Typography variant="body2" noWrap>
                                {props.myTasks ? item.Creator.Username : item.AssignTo.Username}
                              </Typography>
                            </div>
                            <div className="col-6 col-sm-4">
                              <Avatar
                                sx={{
                                  width: 20,
                                  height: 20,
                                  fontSize: '0.9em',
                                  bgcolor: green[400]
                                }}>
                                {props.myTasks
                                  ? item.Creator.Username[0]
                                  : item.AssignTo.Username[0]}
                              </Avatar>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </AccordionSummary>
              <AccordionDetails>
                <div className="row">
                  <div className="col-12 px-5">
                    <Typography variant="body2">{item.Description}</Typography>
                  </div>
                </div>
                {props.myTasks ? (
                  <div className="row">
                    <div className="col-sm text-center">
                      <Button
                        onClick={() => props.dispatchTask({ id: item._id, newState: 1 })}
                        color="warning">
                        درحال انجام
                        <SyncTwoToneIcon className="me-2" />
                      </Button>
                    </div>
                    <div className="col-sm text-center">
                      <Button
                        onClick={() => props.dispatchTask({ id: item._id, newState: 2 })}
                        color="success">
                        انجام شد
                        <TaskAltTwoToneIcon className="me-2" />
                      </Button>
                    </div>
                    <div className="col-sm text-center">
                      <Button
                        onClick={() => props.dispatchTask({ id: item._id, newState: 3 })}
                        color="error">
                        کنسل
                        <CancelTwoToneIcon className="me-2" />
                      </Button>
                    </div>
                  </div>
                ) : (
                  <div className="row">
                    <div className="col-sm text-end pe-3">
                      <IconButton onClick={() => props.deleteTask(item._id)} color="error">
                        <DeleteIcon />
                      </IconButton>
                    </div>
                  </div>
                )}
              </AccordionDetails>
            </Accordion>
          ))
        )}
      </div>
    </div>
  );
}

export default TasksView;
